.program__item {

  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas:
    "img title title title"
    "text text text text"
    " link link link link";
  grid-row-gap: 23px;
  grid-column-gap: 20px;
  align-items: center;

  background-color: @color-basic-gray;
  padding-top: 24px;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.program-item__title {
  grid-area: title;

  font-size: @font-size*2.4;
  text-align: left;
  text-transform: uppercase;
  color: @color-black;
}

.program-item__img-wrapper {
  grid-area: img;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 50px;
  height: 50px;
  margin-left: 2px;

  border-radius: 50%;
  background-color: @color-basic-green;
}

.program-item__img {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.program-item__description {
  margin: 0;
  grid-area: text;
  line-height: 18/14;
}

.program-item__footer {
  grid-area: link;
  width: 100%;
  padding-top: 18px;
  padding-bottom: 23px;

  border-top: 1px solid @color-gray;
}

.program-item__link {
  display: block;
  font-family: @font;
  font-weight: 400;
  font-size: @font-size*1.6;
  text-transform: uppercase;
  text-decoration: none;
  color: @color-black;
  line-height: 1;
}

.program-item__arrow {
  margin-left: 6px;
  margin-bottom: 2px;
}

.arrow {
  display: block;
}

.arrow-long {
  display: none;
}

.program-item__link:hover .arrow {
  display: none;
}

.program-item__link:hover .arrow-long {
  display: block;
}

.program-item__link:active {
  opacity: 0.3;
}
